<script>
import TableComponent from "@/components/Table/Table.vue";
import Pagination1 from "@/components/Pagination1/Pagination1.vue";
import Upload from "@/components/Upload/Upload.vue";
import config from "../../../../public/config/customSetting.json";

export default {
  name: "extensionApply-component",
  components: { Upload, TableComponent, Pagination1 },
  data() {
    return {
      form: {
        add4Apply: [], // 可申請展延清單
        insPeople: [], // 保險展延人員
      },
      rules: {
        add4Apply: [{ required: true, message: "必填", trigger: "change" }],
      },

      insApplierTableColumns: [
        { id: "index", label: "項次", width: "75" },
        { id: "create_date", label: "申請日期", width: "125" },
        { id: "personInfo", label: "姓名(性別)身分證字號", width: "180" },
        { id: "file_Name", label: "附檔", width: "" },
        { id: "period", label: "保險展延期間(起~迄)", width: "" },
        { id: "approve_Status2", label: "審核狀態", width: "125" },
        { id: "reject_Reason", label: "備註", width: "200" },
        { id: "name", label: "審核人員", width: "125" },
      ],
    };
  },
  computed: {
    insChoiceList() {
      return this.$store.state.ins_choicelist;
    },
    // 保險展延申請資料 (All)
    insApplier() {
      return this.$store.state.ins_applier;
    },
    // 保險展延申請資料 (單頁)
    insApplierList() {
      return this.$store.state.ins_applierlist;
    },
  },
  methods: {
    // 將所選的人員代入表格中
    add4ApplyHandler() {
      this.form.insPeople = [];
      for (let i = 0; i < this.form.add4Apply.length; i++) {
        let item = this.insChoiceList.filter(
          (e) => e.id == this.form.add4Apply[i]
        )[0];
        this.form.insPeople.push(item);
      }
    },

    async insFileChgHandler(file, index) {
      let raw = file[0].raw;
      let base64File = await this.toBase64(raw);

      // 取得檔名及 base64 轉檔，並移除欄位驗證
      this.form.insPeople[index].fileName = file[0].name;
      this.form.insPeople[index].fileBase64 = base64File;
      this.$refs.form.clearValidate("insPeople." + index + ".fileName");
    },
    insFileRmvHandler(file, index) {
      this.form.insPeople[index].fileName = "";
      this.form.insPeople[index].fileBase64 = "";
      this.$refs.form.validateField("insPeople." + index + ".fileName");
    },
    toBase64(file) {
      return new Promise((resolve, reject) => {
        let reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(reader.result);
        reader.onerror = (error) => reject(error);
      });
    },

    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.form.insPeople = [];
    },
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          try {
            const res = await this.$store.dispatch(
              "addInsApplier",
              this.form.insPeople
            );
            if (res.data.isOK) {
              this.$message({
                type: "success",
                duration: 5000,
                showClose: true,
                message: "申請成功",
              });

              // 清除表單，並更新申請列表 (呈現第一頁)
              this.resetForm(formName);
              await this.$store.dispatch("getInsApplier");
              this.$store.commit("CURRENT_PAGE_INIT");
            }
          } catch (err) {
            console.log(err);
          }
        } else {
          this.$message({
            type: "error",
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },

    // 從對應 controller 下載文件，並指定檔名
    async downloadPdf(item) {
      try {
        const res = await this.$store.dispatch("commonDownloadPdf", {
          file_id: item.file_Id,
          router_Name: config.getInsuranceArea,
        });
        const href = URL.createObjectURL(res.data);
        const link = document.createElement("a");
        link.href = href;
        link.setAttribute("download", `${item.file_Name}`);
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        URL.revokeObjectURL(href);
      } catch (err) {
        console.log(err);
      }
    },
    changePage(page) {
      this.$store.commit("CHANGE_PAGE_HANDLER", {
        page,
        paramsDataName: "ins_applier",
        paramsListName: "ins_applierlist",
      });
    },
  },
  async mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;

    // 打 API 取得可申請展延人員、保險申請資料
    try {
      await this.$store.dispatch("getInsForChoice");
      await this.$store.dispatch("getInsApplier");
    } catch (err) {
      console.log(err);
    }
    this.$store.commit("CURRENT_PAGE_INIT");
  },
};
</script>

<template src="./extensionApply.html"></template>
